* {
  font-family: 'TT Lakes', sans-serif;
  margin: 0;
  padding: 0; }

body {
  position: relative; }

.main {
  padding-right: 20px;
  background-color: #F8F8F8; }

.wrapper {
  overflow: hidden;
  width: 100%; }
  .wrapper .upper-menu {
    display: flex;
    align-items: center;
    height: 88px;
    justify-content: space-evenly;
    background-color: #ffffff;
    border-bottom: 1px solid #D9FFF5;
    margin: 0;
    padding: 0; }

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
  padding: 16px 0; }
  .container .container__menu {
    display: none; }
  .container .main__divider {
    margin-right: 20px; }
  .container .main-text {
    font-size: 28px;
    font-weight: 700;
    line-height: 40px; }

.slide-menu {
  display: flex;
  overflow-x: auto;
  margin-bottom: 24px; }
  .slide-menu .slide-menu__part {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0;
    padding: 0; }
    .slide-menu .slide-menu__part .slide-menu__item {
      list-style: none;
      border: 2px solid transparent;
      box-sizing: border-box;
      border-radius: 6px;
      background-color: #F8F8F8;
      color: #7E7E82;
      font-weight: 500;
      font-size: 16px;
      padding: 8px; }
      .slide-menu .slide-menu__part .slide-menu__item:hover {
        border: 2px solid #B8FFEC;
        border-radius: 6px;
        background-color: #ffffff; }
      .slide-menu .slide-menu__part .slide-menu__item .slide-menu__text {
        white-space: nowrap;
        text-decoration: none;
        color: #7E7E82; }

.block-1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-shrink: 0;
  flex-grow: 1; }
  .block-1 .block-1__info:first-child {
    margin: 0 16px;
    padding: 0 -16px; }
  .block-1 .block-1__info {
    display: flex;
    flex-direction: column; }
    .block-1 .block-1__info #block-1__read {
      display: none; }
    .block-1 .block-1__info .block-1__text {
      overflow: hidden;
      margin: 11px 0 0 0;
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      text-align: left;
      color: #1B1C21; }
    .block-1 .block-1__info .block-1__text--hidden {
      margin-top: 20px; }
  .block-1 .block-1__btn {
    width: 132px;
    height: 24px;
    margin: 16px 0;
    display: flex;
    align-items: center;
    border: transparent;
    background-color: #F8F8F8; }
    .block-1 .block-1__btn .block-1__btn-icon {
      padding: 0 8px; }
    .block-1 .block-1__btn .block-1__btn-text {
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      white-space: nowrap;
      text-decoration: none;
      letter-spacing: -0.2px;
      text-align: left;
      color: #1B1C21;
      background-color: #F8F8F8;
      display: flex;
      align-items: center; }

.upper-menu__item--hidden,
.block-1__text--hidden,
.block-1__text-full--hidden {
  display: none; }

@media (min-device-width: 768px) {
  .upper-menu__item--hidden {
    display: inline-block; }
  .empty {
    width: 250px; }
  .slide-menu__part {
    margin-left: 24px;
    justify-content: space-around; }
    .slide-menu__part .slide-menu__item:first-child {
      margin-left: 0; }
  .block-1 {
    flex-direction: row;
    justify-content: space-around; }
    .block-1 .block-1__info:first-child {
      width: 40%;
      min-width: 312px;
      margin-right: 48px;
      margin-left: 24px;
      flex-grow: 1; }
      .block-1 .block-1__info:first-child .block-1__text--hidden {
        display: block; }
    .block-1 .block-1__info:last-child {
      min-width: 360px;
      height: 208px; } }

@media (min-device-width: 1366px) {
  .header {
    display: none; }
  .container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    padding-top: 42px;
    margin-bottom: 11px; }
    .container .main__divider {
      margin-right: 30px; }
    .container .container__menu {
      display: flex;
      margin-left: auto; }
      .container .container__menu .container__item {
        display: flex;
        flex-direction: row;
        align-items: center; }
        .container .container__menu .container__item:first-child {
          margin-right: 8px; }
        .container .container__menu .container__item .container__item-text {
          margin-right: 8px;
          display: block;
          font-size: 16px;
          text-decoration: none;
          font-weight: 500;
          vertical-align: middle;
          color: #1B1C21; }
  .slide-menu {
    flex-direction: column;
    align-items: flex-start; }
    .slide-menu .slide-menu__part {
      margin-left: 32px; }
  .block-1 {
    flex-direction: row;
    justify-content: space-around; }
    .block-1 .block-1__info:first-child {
      width: 490px; }
      .block-1 .block-1__info:first-child .block-1__text--hidden,
      .block-1 .block-1__info:first-child .block-1__text-full--hidden {
        display: inline; }
      .block-1 .block-1__info:first-child .block-1__btn {
        margin-top: 13px; }
      .block-1 .block-1__info:first-child .block-1__text:first-child {
        margin-bottom: 24px; }
  .slide-menu {
    overflow-x: hidden; }
    .slide-menu .slide-menu__part--hidden {
      display: flex; }
    .slide-menu .slide-menu__part {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      font-weight: 500;
      font-size: 16px;
      flex-shrink: 0;
      flex-wrap: wrap; } }
