.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.96);
  opacity: 0;
  visibility: hidden;
  top: 0;
  left: 0;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.8s ease 0s;
  z-index: 10; }
  .popup:target {
    opacity: 1;
    visibility: visible; }
  .popup .area {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }
  .popup .popup__body {
    min-height: 100%;
    display: flex;
    justify-content: flex-end; }
    .popup .popup__body .popup__content {
      background-color: #ffffff;
      max-width: 470px;
      position: relative; }

.close-btn img {
  margin: 24px 0 40px 16px;
  vertical-align: top;
  display: block; }

.fieldset {
  display: flex;
  flex-direction: column;
  border: transparent;
  position: relative; }
  .fieldset .fieldset__input {
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    min-height: 48px;
    margin: 0 16px 16px 16px; }
    .fieldset .fieldset__input::-webkit-input-placeholder {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #B5B6BC;
      padding: 12px 16px; }
    .fieldset .fieldset__input::-moz-placeholder {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #B5B6BC;
      padding: 12px 16px; }
    .fieldset .fieldset__input:-ms-input-placeholder {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #B5B6BC;
      padding: 12px 16px; }
    .fieldset .fieldset__input::placeholder {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #B5B6BC;
      padding: 12px 16px; }
  .fieldset .input-4 {
    height: 118px;
    flex-shrink: 0; }
  .fieldset .personal-consent {
    color: #7E7E82;
    font-weight: 500;
    letter-spacing: -0.2px;
    padding: 0 16px 19px 16px; }
    .fieldset .personal-consent a {
      color: #ff1493;
      text-decoration: none; }
      .fieldset .personal-consent a:hover {
        text-decoration: underline; }
  .fieldset .send-btn {
    position: absolute;
    top: 100%;
    right: 5%; }

@media (min-device-width: 768px) {
  .popup__content {
    min-height: 100vh;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); } }

@media (min-device-width: 1120px) {
  .popup__close {
    position: absolute;
    top: 0.5em;
    left: -5em; } }

* {
  font-family: 'TT Lakes', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none; }

body {
  width: 100%; }

.main-part__header {
  position: relative; }
  .main-part__header::before {
    content: '';
    position: absolute;
    top: 0;
    display: block;
    width: 4px;
    height: 40px;
    background-color: #41F6D7; }
  .main-part__header .main-part__text {
    font-size: 24px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -.6;
    padding: 0 0 24px 16px; }

.btn {
  background-color: #ffffff;
  border: none; }

@media (min-device-width: 1120px) {
  .main-part .main-part__header .main-part__text {
    padding: 0 0 24px 30px; }
  .main-part .fieldset .fieldset__input {
