.side-container {
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  top: 0;
  left: 0;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.8s ease 0s;
  z-index: 10; }
  .side-container:target {
    opacity: 1;
    visibility: visible; }
  .side-container .side-container__content {
    max-width: 470px;
    position: relative; }

.close-btn img {
  vertical-align: top;
  display: block; }
