* {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: 'TT Lakes', sans-serif; }

.section {
  position: relative;
  width: 100%;
  overflow: hidden; }
  .section .section__text-container {
    background: linear-gradient(270deg, #F8F8F8 0%, #FFFFFF 98.61%); }
    .section .section__text-container::before {
      display: block;
      width: 100%;
      height: 1px;
      position: absolute;
      content: '';
      left: 0;
      top: 0;
      background: #D9FFF5; }
    .section .section__text-container .section__text {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      text-transform: uppercase;
      padding: 16px;
      letter-spacing: -0.2px;
      color: #7E7E82; }
  .section .swiper {
    height: 134px;
    width: 100%;
    background: #F2F2F2; }
    .section .swiper .pgn-1 {
      position: absolute;
      top: 100px; }
    .section .swiper .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background: #B5B6BC; }
    .section .swiper .swiper-scrollbar {
      display: none; }
    .section .swiper .section-logos__wrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      flex-shrink: 1;
      flex-wrap: nowrap;
      padding: 16px;
      margin: 0 0 0 24px; }
      .section .swiper .section-logos__wrapper .swiper-slide > img {
        width: 240px;
        height: 72px; }

.alternative-container {
  display: none; }
  .alternative-container .container-btn {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 24px; }

@media (min-device-width: 768px) {
  .section .alternative-container {
    display: block;
    background: #F2F2F2;
    padding-top: 24px; }
    .section .alternative-container .alternative-container__btn {
      background: #F2F2F2; }
    .section .alternative-container .container-btn {
      background-color: #f2f2f2; }
      .section .alternative-container .container-btn .btn__icon {
        padding: 0 8px; }
        .section .alternative-container .container-btn .btn__icon .btn__text {
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
          letter-spacing: -0.2px;
          text-align: left;
          color: #1B1C21;
          background-color: #f2f2f2;
          text-decoration: none;
          display: flex;
          align-items: center; }
    .section .alternative-container .alternative-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      flex-shrink: 0;
      overflow: hidden; }
      .section .alternative-container .alternative-wrapper .alternative-wrapper__block {
        display: flex;
        flex-direction: row;
        overflow: hidden;
        margin: 0 12px;
        justify-content: space-between; }
        .section .alternative-container .alternative-wrapper .alternative-wrapper__block .alternative-slide img {
          margin: 16px 12px;
          width: 224px;
          height: 72px; }
  .section .section__text-container .section__text {
    padding-left: 24px; }
  #more {
    display: none; }
  .alternative-slide_hidden {
    display: none; } }

@media (min-device-width: 1120px) {
  .section .section__text-container .section__text {
    padding-left: 32px; }
  #more .alternative-wrapper__block_hidden {
    display: none; }
  .section .alternative-container .alternative-wrapper {
    overflow: hidden; }
    .section .alternative-container .alternative-wrapper .alternative-wrapper__block .alternative-slide img {
      overflow: hidden; }
    .section .alternative-container .alternative-wrapper .alternative-wrapper__block .alternative-slide_hidden {
      display: flex; } }
