* {
  font-family: 'TT Lakes', sans-serif;
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box; }

body {
  position: relative;
  width: 768px;
  color: #1B1C21;
  background-color: #f8f8f8; }
  body .body-wrapper {
    position: relative;
    margin: 0 auto;
    width: 100%;
    background-color: #ffffff;
    overflow: auto; }
    body .body-wrapper .side-container {
      position: fixed;
      background-color: rgba(255, 255, 255, 0.96);
      width: 100%;
      max-height: 100vh;
      opacity: 0;
      visibility: hidden;
      top: 0;
      left: 0;
      overflow-y: auto;
      overflow-x: hidden;
      transition: all 0.8s ease 0s;
      z-index: 10; }
      body .body-wrapper .side-container .area {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0; }
      body .body-wrapper .side-container:target {
        opacity: 1;
        visibility: visible; }
      body .body-wrapper .side-container .side-container__content {
        box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2);
        background-color: #ffffff;
        width: 320px;
        position: relative; }
        body .body-wrapper .side-container .side-container__content .side-container__main {
          min-height: 50vh; }
        body .body-wrapper .side-container .side-container__content .side-container__header {
          position: relative;
          width: auto;
          height: 116px; }
          body .body-wrapper .side-container .side-container__content .side-container__header .header__wrapper {
            position: absolute;
            top: 2em;
            left: 1.2em;
            display: flex;
            width: 272px;
            align-items: center; }
            body .body-wrapper .side-container .side-container__content .side-container__header .header__wrapper .icon:hover {
              transform: scale(1.2);
              transition-duration: .3s; }
            body .body-wrapper .side-container .side-container__content .side-container__header .header__wrapper .header__icon:first-child {
              margin-right: 16px; }
            body .body-wrapper .side-container .side-container__content .side-container__header .header__wrapper .header__icon:nth-child(2) {
              margin-right: 78px; }
      body .body-wrapper .side-container .sub-menu {
        position: relative;
        width: 222px;
        max-height: 1024px; }
        body .body-wrapper .side-container .sub-menu .sub-menu__item {
          display: block;
          white-space: nowrap;
          list-style: none;
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
          letter-spacing: -.2px;
          margin-bottom: 1.3em;
          margin-left: 24px;
          cursor: pointer; }
        body .body-wrapper .side-container .sub-menu .sub-menu__item:hover {
          color: #7E7E82;
          opacity: 0.7;
          white-space: nowrap;
          transition-property: all;
          transition-duration: 1s; }
        body .body-wrapper .side-container .sub-menu .sub-menu__item:hover::before {
          display: inline-block;
          width: 4px;
          height: 32px;
          background-color: #41F6D7;
          content: '';
          position: absolute;
          left: 0.5%; }
      body .body-wrapper .side-container .aside-footer {
        width: auto; }
        body .body-wrapper .side-container .aside-footer .aside-footer__wrapper {
          padding-left: 24px;
          padding-bottom: 32px;
          height: 100vh; }
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .socials {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            margin-bottom: 16px; }
            body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .socials .social {
              margin-right: 16px; }
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .email {
            font-size: 16px;
            font-weight: 500;
            line-height: 24px;
            letter-spacing: -0.2px;
            text-align: left;
            margin-bottom: 12px; }
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .phone {
            font-size: 24px;
            line-height: 32px;
            letter-spacing: -0.6px;
            text-align: left;
            text-decoration: none;
            margin-bottom: 40px;
            font-weight: 700; }
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .email a,
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .phone a,
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .lang a {
            text-decoration: none;
            color: #1B1C21; }
          body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .lang {
            font-size: 16px;
            line-height: 24px;
            letter-spacing: -0.2px;
            text-align: left;
            font-weight: 500; }
            body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .lang .lang__selector {
              margin-right: 7px; }
            body .body-wrapper .side-container .aside-footer .aside-footer__wrapper .lang .lang__selector:hover {
              color: #7E7E82; }

@media (min-device-width: 1366px) {
  body {
    background-color: #f8f8f8; }
    body .body-wrapper {
      position: relative;
      margin: 0 auto;
      width: 100%;
      background-color: #ffffff;
      overflow: auto; }
      body .body-wrapper .body-intro {
        min-width: 1046px;
        margin: 0 auto;
        padding-left: 320px; }
      body .body-wrapper .side-container {
        opacity: 1;
        width: 320px;
        visibility: visible;
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh; }
        body .body-wrapper .side-container area {
          display: none; } }
